
import {defineComponent} from 'vue';

export default defineComponent({
    name: 'ForzaTelemetry',
    data() {
        return {
            telemetry: {},
        };
    },
    mounted() {
        const ws = new WebSocket('ws://localhost:3001');
        ws.onmessage = (payload) => {
            this.telemetry = JSON.parse(payload.data);
        }
    },
});
